import React from "react";
import { Section, LHS, RHS, TextColor } from "./general/style/common";
import { ModelDetails, Review } from "./general/style/style";
import { DhProperties } from "../DhProperties";


class ReviewStarPanel extends React.Component {

    onWriteReviewClicked = () => {
       // console.log("ReviewStarPanel:id= ", this.props.dhPostId)
        const story = {
          id: this.props.dhPostId,
          deeplinkUrl:
            DhProperties && DhProperties.seo_url
              ? DhProperties.seo_url
              : "http://m.dailyhunt.in/news/india/english/dspark-epaper-dspark/maruti+suzuki+vitara+brezza-newsid-natznencarsm1m350",
          sectionAndNamespace: "NEWS",
        };
    
        const id = story ? story.id : undefined;
        const sectionAndNamespace = story && story.format == "VIDEO" ? "VIDEO" : "NEWS";
        const deeplinkUrl = story ? story.deeplinkUrl : "";
        const payload = {
          section: sectionAndNamespace,
          referrer: "STORY_DETAIL",
          referrerId: id ? "storydetail&&" + id : "",
          commentParams: {
            namespace: sectionAndNamespace,
            id: id,
          },
        };
        const reviewDeeplink = deeplinkUrl + "?allComments=" + encodeURIComponent(JSON.stringify(payload));
        // console.log("handleAction for onCommentClick", commentDeeplink);
        typeof newsHuntAction != "undefined" && newsHuntAction.handleAction(reviewDeeplink);
      };

    render() {
        return (
            <Section padding="0 16px">  
            <ModelDetails>
            <LHS>
                <Review>
                <span className={this.props.starClassName}></span> 
                <p>{this.props.reviews}</p>
                </Review>
            </LHS>
            <RHS>
               {this.props.dhPostId && <TextColor className="red" padding="9px 0" onClick={()=>this.onWriteReviewClicked()}>Write a Review</TextColor> }
            </RHS>
        </ModelDetails> 
        </Section>
        );
    }
}

export default ReviewStarPanel;


